<style>
  /* 公共样式 */
  body{
    margin: 0;
    padding: 0;
  }
  a{
    text-decoration: none;
    cursor: pointer;
  }
  ul{
    list-style: none;
    margin: 0;
    padding: 0;
  }
  span{
    font-size: 12px;
    color: #666666;
  }
  p{
    margin: 0;
    padding: 0;
  }
  input{
    outline: none;
  }
  .div_clear{
    clear: both;
    height: 0px;
  }


  .page{
      min-width: 1200px;
    }
    /* 顶部图片 */
  .top_pic_wrap{
    position: relative;
    height: 60px;
  }
  .top_pic{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-image: url(./../assets/pic01.png);
    background-repeat: no-repeat;
    background-position: center bottom;
  }

  .fade-enter-active, .fade-leave-active{
   transition: all .3s;
  }
  .fade-enter, .fade-leave-to{
   opacity: 0;
  }
</style>

<template>
  <div class="page" @click="resetComponent">
    <!-- 顶部图片 -->
    <div class="top_pic_wrap">
      <div class="top_pic"></div>
    </div>
    <!-- 顶部导航栏 -->
    <toplogin></toplogin>
    
    <!-- 顶部搜索框 -->
    <searchbar></searchbar>

    <!-- 主导航栏 -->
    <topmainnav></topmainnav>

    <transition name="fade" mode="out-in">
        <router-view></router-view>
    </transition>

    <!-- 客服部分 -->
    <kefu></kefu>

    <!-- 返回顶部 -->
    <returntop></returntop>

    <!-- 底部 -->
    <footercomponent></footercomponent>
  </div>
</template>


<script>
  import { eventBus } from '@/eventBus'
  import toplogin from './header/toplogin.vue'
  import searchbar from './header/searchbar.vue'
  import topmainnav from './header/topmainnav.vue'
  import kefu from './public/kefu.vue'
  import returntop from './public/returntop.vue'
  import footercomponent from './footer/footercomponent.vue'

  export default {
    data(){
      return{
        caution: false
      }
    },
    methods: {
      resetComponent() {
        eventBus.$emit('reset-component')
      },
      setCookie(name,value,expires,path,domain,secure){
        var curCookie=name+"="+escape(value) +
        ((expires)?";expires="+expires.toGMTString() : "") +
        ((path)?"; path=" + path : "") +
        ((domain)? "; domain=" + domain : "") +
        ((secure)?";secure" : "")
        if(!this.caution||(name + "=" + escape(value)).length <= 4000){
          document.cookie = curCookie
        }else if(confirm("Cookie exceeds 4KB and will be cut!")){
          document.cookie = curCookie
        }
      },
      getCookie(name){
        var prefix = name + "="
        var cookieStartIndex = document.cookie.indexOf(prefix)
        if (cookieStartIndex == -1){
          return null
        }    
        var cookieEndIndex=document.cookie.indexOf(";",cookieStartIndex+prefix.length)
        if(cookieEndIndex == -1){
          cookieEndIndex = document.cookie.length
        }
        return unescape(document.cookie.substring(cookieStartIndex+prefix.length,cookieEndIndex))
      },
      deleteCookie(name, path, domain){
        if(getCookie(name)) 
        {
        document.cookie = name + "=" + 
        ((path) ? "; path=" + path : "") +
        ((domain) ? "; domain=" + domain : "") +
        "; expires=Thu, 01-Jan-70 00:00:01 GMT"
        }
      },
      fixDate(date){
        var base=new Date(0)
        var skew=base.getTime()
        if(skew>0)
        {
        date.setTime(date.getTime()-skew)
        }    
      }
    },
    created(){
      var now=new Date()
      this.fixDate(now)
      now.setTime(now.getTime()+365 * 24 * 60 * 60 * 1000)
      var visits = this.getCookie("counter")
      if(!visits)
      {
       visits=1;
      }  
      else
      {
       visits=parseInt(visits)+1;
      }  
      this.setCookie("counter", visits, now)
      alert("您是到访的第" + visits + "位用户！")
    },
    components: {
      toplogin,
      searchbar,
      topmainnav,
      kefu,
      returntop,
      footercomponent
    }
  }
</script>


